// Variables
@import 'variables';

.alert-dismissible .close{
  margin: 0;
}
.choose-photo-item .checkbox .checkbox-material .check {
  background: #fff;
}
.choose-photo-item .checkbox .checkbox-material {
  position: absolute;
  top: 10px;
  right: 30px;
  left: auto;
}
.choose-photo-item.game-choose{
  width: 25%;
}
#game-list-container{
  position: relative;
  width: 100%;
  height: 400px;
  margin-bottom: 20px;
}
.friend-requests .notification-icon {
  margin-top: 20px;
}
.news-feed-form .author-thumb{
  top: 18px;
}
.emoji-picker-container{
  width: 100% !important;
  padding-left: 60px;
  padding-right: 7px;
}
.emoji-wysiwyg-editor{
  width: 100% !important;
}
.post-add-icon.active{
  fill:#ff5e3a;
  color:#ff5e3a;
}
.post-block-photo a{
  height: 120px;
  margin-bottom: 10px;
}

/*Upload avatar*/
.top-header.top-header-favorit .top-header-author{
  transform: none;
  max-width: 100%;
  z-index: 22;
}
.top-header.top-header-favorit .author-thumb{
  position: relative;
}
.fileupload{
  position: absolute !important;
  top: 50px;
  left: 40px;
}
.fileupload:hover{
  color:#ff5e3a;
}
.fileupload span{
  display: none;
}
.top-header-thumb .author-thumb:hover span{
  display: block;
  border: 1px solid;
  padding: 3px 6px;
  border-radius: 3px;
  color: #fff;
  cursor: pointer;
}

.top-header-thumb .top-header-overlay{
  z-index: 100;
  max-height: 300px;
  overflow: hidden;
  /*height: 300px;*/
}

.top-header-thumb .top-header-overlay .fileupload{
  right: 40px;
  top: 40px;
  left: inherit;
  z-index: 22;
}

.top-header-thumb .top-header-overlay .fileupload span{
  display: block;
  border: 1px solid;
  padding: 3px 6px;
  border-radius: 3px;
  color: #fff;
  cursor: pointer;
}

.friend-header-thumb{
  max-height: 150px;
}

.g-core-image-upload-form input{
  cursor: pointer;
}

.add-options-message{
  border-top: 1px solid #e6ecf5;
}
.links{
  padding: 0 25px;
}
.post .links{
  padding: 0;
}
.post .links img{
  max-height: 130px;
  max-width: 200px;
  width: auto;
}
.pa-0{
  padding: 0!important;
}
.author-thumb img{
  max-width: 100%;
}

.chat-field .ps{
  height: 350px;
}
#chatsDisplay.ps{
  height: 560px;
}

.notification-list li.active{
  background-color: #fafbfd;
}
.notification-list li{
  padding: 15px 25px;
}
.chat-message-field .chat-message-item{
  margin-top: 5px;
}

.comment-form .reply_on{
  margin-right: 15px;
  float: right;
  margin-bottom: 0;
  margin-top: 17px;
  padding: .8rem 1.1rem;
  font-size: .688rem;
}

.close-reply svg{
  height: .688rem;
}

.back-to-top{
  right: 15px;
}

.chat-dialog-form, .map-popup, .repost-dialog-form{
  width: 600px;
}

.repost-dialog-form .btn{
  float: right;
  margin-bottom: 0;
  margin-top: 15px;
}

.news-feed-form .author-thumb img{
  width: 40px;
  height: 40px;
}

.comments-list .post__author img{
  width: 30px;
  height: 30px;
}

.pin-google{
  color: #7c5ac2;
  padding: 4px 9px;
  width: 20px;
  height: 20px;
  font-size: 14px;
  display: inline-block;
  text-align: center;
  line-height: 20px;
  position: relative;
  top: -1px;
  margin-left: 0;
}

.university-overlay{
  max-height: 300px;
  overflow: hidden;
  border-radius: 5px;
  /*height: 300px;*/
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.verified{
  width: 16px;
  height: 16px;
  border-radius: 100%;
  background-color: #6ec4f7;
  color: #fff;
  font-size: 6px;
  display: inline-block;
  text-align: center;
  line-height: 16px;
  position: relative;
  top: -6px;
  margin-left: 5px;
}

.left-menu .left-menu-icon{
  margin-right: 0;
}

.modal-content{
  width: 100%;
}

.__image_body{
  border: 3px solid #fff;
  cursor: pointer;
}

.university-article .post-thumb{
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.university-article .post-thumb a{
  width: 100%;
}

.form-group.with-button button{
  color: #fff;
  background-color: #002650;
  border-color: #002650;
}

.accept-request{
  background-color: #002650;/*003a50;*/
}

.left-menu li a{
  position: relative;
}

.left-menu li a .label-avatar{
  width: 19px;
  height: 19px;
  line-height: 19px;
  top: 8px;
  right: 10px;
}

.w-personal-info li{
  padding: 8px 0;;
}

.pull-right {
  float: right;
}

.mt-30{
  margin-top: 30px !important;
}

.ui-block-title.ui-block-title-small .title{
  font-size: 11px;
}

/**
 ** Select-2
 */
.select2-container--default .select2-selection--single{
  border: 1px solid #e6ecf5;
}

.select2-container--default .select2-selection--single, .select2-container--default .select2-selection--single .select2-selection__arrow{
  height: 50px;
}
.select2-container--default .select2-selection--single .select2-selection__rendered{
  line-height: 50px;
}

/**
 ** Calendar
 */
.vdp-datepicker__calendar{
  width: 260px !important;
}
.vdp-datepicker.is-invalid input
{
  border-color: #dc3545;
}
.vdp-datepicker__calendar .cell{
  height: 28px !important;
  line-height: 28px !important;
}


.profile-section{
    padding: 20px 0;
    z-index: 30;
}

.isDisabled {
    cursor: not-allowed;
    opacity: 0.5;
}
.isDisabled > a {
    display: inline-block;  /* For IE11/ MS Edge bug */
    pointer-events: none;
    text-decoration: none;
}

.btn.btn-outline-primary {
    color: #002650;
}
.btn.btn-outline-primary:hover{
    color: #fff;
}
a.btn-apply{
    padding: .5rem 1rem;
}



